	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapter 19 – Perfecting skills","texte":{"childMarkdownRemark":{"html":"<p><strong>Two days later at Clara's country house</strong></p>\n<p>Sam was shirtless in the middle of the backyard with Wamien and Tiana sitting on the balcony.</p>\n<p>Wamien: Come on, Sam, focus, you're capable.<br>\nSam: I know Wamien.</p>\n<p>Sam places his wings in his back and concentrates to make them smaller and gradually disappear.</p>\n<p>Tiana: Come on Sam...</p>\n<p>We all know that Sam will be able to walk freely if he learns to retract his wings. They eventually disappear.</p>\n<p>Wamien: Congratulations Sam!<br>\nTiana: Yay.</p>\n<p>We both applaud and Sam slowly opens his eyes and looks over his shoulders to see that his wings have disappeared. He takes an inspiration and his wings come out.</p>\n<p>Wamien: Is this about muscle?<br>\nSam: Yeah, it's less complicated than I thought.</p>\n<p>The three of us laugh and Clara comes in with Sam and Tiana's coffees and gives me my French vanilla.</p>\n<p>Wamien: Thank you Clara.<br>\nClara: A pleasure, sweetie.<br>\nTiana: Sweetie~ Thank you Clara.</p>\n<p>Sam approaches and takes his coffee to start drinking. Tim gets out of the house and joins us.</p>\n<p>Tim: Good morning, folks.</p>\n<p>Everyone says good morning to Tim. Tim, look at Sam.</p>\n<p>Tim: You lost the wings?<br>\nSam: Nope looks.</p>\n<p>He gets both his wings out without any problem.</p>\n<p>Tim: Hm, nice, so you're gonna look normal.<br>\nSam: Yeah.</p>\n<p>Kameron leaves the house with Kémyanne, they hold hands and laugh together. They're cute. I feel a hand slipping into mine. It's Clara, she definitely fell in love with me seeing the stars in her eyes when she looks at me. I let her hold my hand. Kameron goes to the center of the garden and takes off his sweater to send it to Kémy who grabs it by hugging it against her. Kameron takes a deep breath and let the flames be! His upper body turns into a flame and he starts to move. He forms a fireball in his right hand and concentrates on handling it.</p>\n<p>Wamien: Go Kameron, 30 seconds to keep it without physical contact! You're capable!<br>\nKameron: I try.</p>\n<p>He's shaking a little, keeping it a foot above his hand.</p>\n<p>Wamien: Another 20 seconds Kameron, it's fine.<br>\nSam: Come on Kameron.</p>\n<p>Kameron trembles more and more as time collapses.</p>\n<p>Wamien: 10, 9, 8, 7, 6...</p>\n<p>The others join me in the countdown.</p>\n<p>Clara, Tiana, Sam, Tim, Kémy, Wamien: 5, 4, 3, 2, 1, Bravo!</p>\n<p>Kameron makes the fireball disappear and lets his body become normal again. Kémy gets up and comes to give him back his sweater and takes the opportunity to give him a kiss on the cheek. I get up and decide to go home and start making lunch. Clara also gets up and joins me.</p>\n<p>Clara: What do you want to do for lunch?<br>\nWamien: Pancakes for everyone.<br>\nClara: All right, I'll get the stuff out.</p>\n<p>She took out everything she needed to make pancakes. Thalie still gets to sleep, always getting up later than everyone else.</p>\n<p>Thalie: Yo people.<br>\nWamien: Yo Thalie, sleep well?<br>\nThalie: Yeah, where's Louann?<br>\nClara: In the living room, she's watching TV.<br>\nThalie: Hm, okay.</p>\n<p>Thalie smiles a little and goes towards the living room. Louann starts screaming our names.</p>\n<p>Louann: Friends! Come and see quickly!</p>\n<p>Everyone who heard the call is coming. Louann points us to the TV, where there's a news.</p>\n<p>\"Earlier this morning, the Black Eagles struck again, this time they attacked a scientific laboratory in the kingdom of Mahinu. The lab was empty, so there were no injuries at the scene, yet we ask the population to stay outside the perimeter to let the clean-up teams...\"</p>\n<p>Wamien: My eye that there was no one! There were still test subjects in that place.<br>\nClara: Relax, Wamien.<br>\nWamien: We'll take care of these Black Eagles soon.</p>"}},"chapitreAvant":"chapter-18-revelation","chapitreApres":"chapter-20-betrayal","nomRoman":"the-first-cyborg-volume-1","slug":"chapter-19-perfecting-skills","codeChapitre":"H0001C020","equivalentUrl":"/histoires/chapitre/chapitre-19-le-perfectionnement"}},"pathContext":{"slug":"chapter-19-perfecting-skills"}}

